import { aerithOrigin, rod,ObjectType, get } from './model'
import { TAG_EVENT } from './anayltics';
import { Mesh,Object3D, ShaderMaterial, ArrowHelper, Vector3, Euler, DoubleSide, MeshNormalMaterial, MeshBasicMaterial, BackSide, FrontSide, Box3, BoundingBoxHelper } from 'three';
import { GUI } from 'dat.gui/build/dat.gui.js'

var showArrows = false;
var showBoundingBox = false;

export enum ShaderType{
    OUTLINE,
    SELECTED,
    GRAY,
    DEBUG,
}

var GET_SHADER = function(shaderType: ShaderType): ShaderMaterial{
    switch(shaderType){
        case ShaderType.OUTLINE:
            return new OutlineShader();
        case ShaderType.SELECTED:
            return new SelectedShader();
        case ShaderType.DEBUG:
            return new SelectedShaderDebug();
        case ShaderType.GRAY:
        default:
            return new PureOutlineShader();
    }
}

class OutlineShader extends ShaderMaterial{
    constructor(){
       super( {
	   
            vertexShader:  "attribute vec3 bary;\
                            attribute vec3 moveCoords; \
                            attribute float hasLines;\
                            varying vec3 vBC;\
                            varying float hasLine;\
                            \
                            void main() { \
                                gl_Position = projectionMatrix * modelViewMatrix * vec4(position,1); \
                                vBC = vec3(bary.x + moveCoords.x ,bary.y+ moveCoords.y ,bary.z+ moveCoords.z);\
                                hasLine = hasLines;\
                            }",
    
            fragmentShader: "varying vec3 vBC; \
                            varying float hasLine; \
                            float edgeFactor(){ \
                                vec3 a3 = smoothstep(vec3(0.0), fwidth(vBC)*1.5, vBC); \
                                return min(min(a3.x, a3.y), a3.z);\
                            } \
                            \
                            void main() { \
                                float edge = edgeFactor();\
                                float edgeAdj = edge;\
                                vec4 blue = vec4(0.39215686274,0.58431372549,0.9294117647,1);\
                                vec4 edgeColor = vec4(edgeAdj - 1.0 + blue.r,edgeAdj - 1.0 + blue.g,edgeAdj - 1.0 + blue.b,1.0);\
                                vec4 blendedEdge = edgeColor * (hasLine) + blue * (1.0 - hasLine);\
                                gl_FragColor = blendedEdge;\
                            } "
                        
        } );

        this.extensions.derivatives = true;
        this.side = DoubleSide

    }
}



class SelectedShader extends ShaderMaterial{
    constructor(){
       super( {
	   
        vertexShader:  "attribute vec3 bary;\
        attribute vec3 moveCoords; \
        attribute float hasLines;\
        varying vec3 vBC;\
        varying float hasLine;\
        \
        void main() { \
            gl_Position = projectionMatrix * modelViewMatrix * vec4(position,1); \
            vBC = vec3(bary.x + moveCoords.x ,bary.y+ moveCoords.y ,bary.z+ moveCoords.z);\
            hasLine = hasLines;\
        }",

        fragmentShader: "varying vec3 vBC; \
        varying float hasLine; \
        float edgeFactor(){ \
            vec3 a3 = smoothstep(vec3(0.0), fwidth(vBC)*1.5, vBC); \
            return min(min(a3.x, a3.y), a3.z);\
        } \
        \
        void main() { \
            float edge = edgeFactor();\
            float edgeAdj = edge;\
            vec4 green = vec4(0.6,0.7,0.6,1.0);\
            vec4 edgeColor = vec4(edgeAdj - 1.0 + green.r,edgeAdj - 1.0 + green.g,edgeAdj - 1.0 + green.b,1.0);\
            vec4 blendedEdge = edgeColor * (hasLine) + green * (1.0 - hasLine);\
            gl_FragColor = blendedEdge;\
        } "
                        
        } );

        this.extensions.derivatives = true;
        this.side = DoubleSide

    }
}

class SelectedShaderDebug extends ShaderMaterial{
    constructor(){
       super( {
	   
            vertexShader:  "attribute vec3 bary;\
                            attribute vec3 moveCoords; \
                            varying vec3 vBC;\
                            \
                            void main() { \
                                gl_Position = projectionMatrix * modelViewMatrix * vec4(position,1); \
                                vBC = vec3(bary.x,bary.y ,bary.z);\
                            }",
    
            fragmentShader: "varying vec3 vBC; \
                            \
                            float edgeFactor(){ \
                                vec3 a3 = smoothstep(vec3(0.0), fwidth(vBC)*1.5, vBC); \
                                return min(min(a3.x, a3.y), a3.z);\
                            } \
                            \
                            void main() { \
                                float edge = edgeFactor();\
                                vec3 edgeColor = vec3(edgeFactor() -.4,edgeFactor() -.4,0.7);\
                                gl_FragColor.rbg = edgeColor;\
                            } "
                        
        } );

        this.extensions.derivatives = true;
        this.side = DoubleSide

    }
}


class PureOutlineShader extends ShaderMaterial{
    constructor(){
        super( {
	   
            vertexShader:  "void main() { \
                                gl_Position = projectionMatrix * modelViewMatrix * vec4(position,1);\
                            }",
    
            fragmentShader: "void main() { \
                                vec3 blue = vec3(0.39215686274,0.58431372549,0.9294117647);\
                                gl_FragColor.rgb = blue;\
                            }"
                        
        });

        this.extensions.derivatives = true;
        this.side = DoubleSide
    }
}

export class ThreeArrows extends Object3D {

    arrowHelper: ArrowHelper;
    arrowHelper2: ArrowHelper;
    arrowHelper3: ArrowHelper;
    showingArrows: boolean = true;

    constructor(showArrows: boolean) {
        super();

        var dir = new Vector3(0, 1, 0).normalize();
        var dir2 = new Vector3(1, 0, 0).normalize();
        var dir3 = new Vector3(0, 0, 1).normalize();
        var origin = new Vector3(0, 0, 0);

        var length = 1;

        var yellow = 0xffff00;
        var red = 0xff0000;
        var blue = 0x0000ff;

        this.arrowHelper = new ArrowHelper(dir, origin, length, yellow);
        this.arrowHelper2 = new ArrowHelper(dir2, origin, length, red);
        this.arrowHelper3 = new ArrowHelper(dir3, origin, length, blue);
        
        this.add(this.arrowHelper);
        this.add(this.arrowHelper2);
        this.add(this.arrowHelper3);
        this.showingArrows = showArrows;
        this.showArrows(this.showingArrows);

    }

    showArrows(show) {

        this.showingArrows = show;

        this.arrowHelper.visible = this.showingArrows;
        this.arrowHelper2.visible = this.showingArrows;
        this.arrowHelper3.visible = this.showingArrows;

        TAG_EVENT("App::ShowPivots");
    }

    showHideArrows() {
        this.showArrows(!this.showingArrows);
    }
}

export class BodyJSONObject {
    name: string;
    position: Vector3;
    scale: Vector3;
    pivot: Vector3;
    rotation: Euler;
    children: Array<BodyJSONObject>;
    type: string;
    canMovePosition: boolean;
    canRotate: boolean;
    right: boolean;

    constructor(object) {
        this.name = object.name;
        this.position = new Vector3(object.position.x, object.position.y, object.position.z);
        this.scale = new Vector3(object.scale.x, object.scale.y, object.scale.z)
        this.pivot = new Vector3(object.pivot.x, object.pivot.y, object.pivot.z)
        this.rotation = new Euler(object.rotation.x, object.rotation.y, object.rotation.z)
        
        this.children = [];

        for (var i = 0; i < object.children.length; i++) {
            var childAtIndex = object.children[i];
            var objectUsingData = new BodyJSONObject(childAtIndex);
            this.children.push(objectUsingData);
        }

        this.canMovePosition = object.config.canMovePosition;
        this.canRotate = object.config.canRotate;
        this.right = (object.config.right != undefined);
        this.type = object.type;
    }
}

export enum BodyType {
    FEMALE_STYLISED,
    ROD
}

export class MeshWithParent extends Mesh{

    parentRef: PivotedBodyPart;

    constructor(geometry,shader,parent){
        super(geometry,shader)
        this.parentRef = parent;
    }
}

export class PivotedBodyPart extends ThreeArrows {

    meshType: string;
    bodyPart: Mesh;
    boundingBox: Object3D;
    canMove: boolean;
    canRotate: boolean;
    pivot: Vector3;
    showMesh: boolean = true;
    showBoundingBox: boolean = false;

    constructor(data: BodyJSONObject, parentPivot: Vector3) {
        super(showArrows);

        this.pivot = new Vector3();
        this.pivot.x = data.pivot.x;
        this.pivot.y = data.pivot.y;
        this.pivot.z = data.pivot.z;

        this.meshType = data.type;

        this.position.x = data.pivot.x - parentPivot.x;
        this.position.y = data.pivot.y - parentPivot.y;
        this.position.z = data.pivot.z - parentPivot.z;

        this.canMove = data.canMovePosition;
        this.canRotate = data.canRotate;

        var objectType: ObjectType = ObjectType.CUBE;
        var shaderType: ShaderType = ShaderType.OUTLINE;

        switch (data.type) {
            case "head":
                objectType = ObjectType.HEAD;
                break;

            case "waist":
                objectType = ObjectType.WAIST;
                break;

            case "feet":
                objectType = ObjectType.FEET;
                break;

            case "calves":
                objectType = ObjectType.CALVES;
                break;

            case "hands":
                objectType = ObjectType.HANDS;
                break;

            case "torso":
                objectType = ObjectType.TORSO;
                break;

            case "thigh":
                objectType = ObjectType.THIGH;
                break;

            case "forearm":
                objectType = ObjectType.FOREARM;
                break;

            case "upperarm":
                objectType = ObjectType.UPPERARM;
                break;
            case "shoulder":
                objectType = ObjectType.SHOULDER;
                break;
            default:
                break;
        }

        this.bodyPart = new MeshWithParent(get(objectType, data.scale.x, data.scale.y, data.scale.z,data.right), 
            GET_SHADER(shaderType),this);

        this.bodyPart.rotation.x = data.rotation.x;
        this.bodyPart.rotation.y = data.rotation.y;
        this.bodyPart.rotation.z = data.rotation.z;

        this.bodyPart.position.x = data.position.x - data.pivot.x;
        this.bodyPart.position.y = data.position.y - data.pivot.y;
        this.bodyPart.position.z = data.position.z - data.pivot.z;

        this.add(this.bodyPart);

        var material = new MeshNormalMaterial();
	
        var outlineMaterial1 = new MeshBasicMaterial( { color: 0x000000, side: BackSide } );
        var z = data.scale.z* 1.05;
        if (data.right){
            z *= -1;
            outlineMaterial1 = new MeshBasicMaterial( { color: 0x000000, side: FrontSide} );

        } 


        this.boundingBox = new BoundingBoxHelper( this.bodyPart, 0x000000 );
        this.boundingBox.visible = showBoundingBox;

        this.add( this.boundingBox);

        var outlineMesh1 = new Mesh( get(objectType, data.scale.x * 1.05, data.scale.y* 1.05, z,data.right), outlineMaterial1 );

        
        this.bodyPart.add( outlineMesh1 );
    }

    setSelected(selected) {

        var shaderType = ShaderType.OUTLINE;

        if (selected) {
            shaderType = ShaderType.SELECTED;
        }
        else {
            shaderType = ShaderType.OUTLINE;
        }

        this.bodyPart.material = GET_SHADER(shaderType);
    }

    addChildren(data: BodyJSONObject, hash: string) {

        for (var i = 0; i < data.children.length; i++) {
            var childSource = data.children[i];
            var newSceneObject = new PivotedBodyPart(childSource, data.pivot);
            newSceneObject.addChildren(childSource, hash);

            this.add(newSceneObject);


        }
    }


    showHideArrowsRecursive(){
        this.showHideArrows();

        for (var i = 0; i < this.children.length; i++) {
            var child = this.children[i];

            if (child instanceof PivotedBodyPart){
                (child as PivotedBodyPart).showHideArrowsRecursive();
            }
        }
    }

    showHideBoundingBoxRecursive(){
        this.showBoundingBox = !this.showBoundingBox
        this.boundingBox.visible = this.showBoundingBox;

        for (var i = 0; i < this.children.length; i++) {
            var child = this.children[i];

            if (child instanceof PivotedBodyPart){
                (child as PivotedBodyPart).showHideBoundingBoxRecursive();
            }
        }
    }

    showHideMeshRecursive(){
        this.showMesh = !this.showMesh;
        this.bodyPart.visible = this.showMesh;

        for (var i = 0; i < this.children.length; i++) {
            var child = this.children[i];

            if (child instanceof PivotedBodyPart){
                (child as PivotedBodyPart).showHideMeshRecursive();
            }
        }
    }

    bindGUI(gui: GUI){
        gui.add(this, 'showHideArrowsRecursive').name("Toggle Pivots");
        gui.add(this, 'showHideBoundingBoxRecursive').name("Toggle Bounding Box");
        gui.add(this, 'showHideMeshRecursive').name("Toggle Mesh");

    }


}


export class LocalBodySource {

    getBody(bodyType: BodyType, hash: string) {

        var sourceObject: BodyJSONObject = new BodyJSONObject(aerithOrigin);;

        switch (bodyType) {
            case BodyType.FEMALE_STYLISED:
                sourceObject = new BodyJSONObject(aerithOrigin);
                break;
            case BodyType.ROD:
                sourceObject = new BodyJSONObject(rod);
                break;
        }

        var originObject: PivotedBodyPart = new PivotedBodyPart(sourceObject, new Vector3(0,0,0));
        originObject.addChildren(sourceObject, hash);


        return originObject;
    }

}




