import './style.css'
import { PerspectiveCamera, Scene, Color, Vector2, WebGLRenderer, TransformControls, GridHelper, Raycaster, Mesh } from 'three';
import * as THREE from 'three'

import { GUI } from 'dat.gui/build/dat.gui.js'
import { LocalBodySource, BodyType, PivotedBodyPart, MeshWithParent } from './objects';
import { TAG_EVENT } from './anayltics';
var orbit = require ('./orbit');
var gizmo = require ('./selectionGizmo');
'use strict';

///////////////////////////////////////////////////////////////////////
// TRANSFORM GIZMO
///////////////////////////////////////////////////////////////////////



///////////////////////////////////////////////////////////////////////
// SCENE CONFIG
///////////////////////////////////////////////////////////////////////

var numberOfAdds = 0;
var numberOfFmAdds = 0;

var tempHash = "aerith";

var dragFlag = 0;

function onMouseDown( event ) {
	dragFlag = 0;
}

function onMouseMove( event ) {
	dragFlag = 1;
}

var mouse = new Vector2();

function onMouseClicked( event ) {
	mouse.x = ( event.clientX / window.innerWidth ) * 2 - 1;
	mouse.y = - ( event.clientY / window.innerHeight ) * 2 + 1;
}

window.addEventListener( 'mousedown', onMouseDown, false );
window.addEventListener( 'mousemove', onMouseMove, false );
window.addEventListener( 'mouseup', onMouseClicked, false );
window.addEventListener( 'touchstart', onMouseClicked, false);

let scene = new Scene();
scene.background = new Color( 0x6495ed );

let fieldOfView = 50;

let camera = new PerspectiveCamera(fieldOfView, window.innerWidth / window.innerHeight, 0.1, 1000)
orbit()(camera);

camera.position.x = -9.06;
camera.position.y = 9.85;
camera.position.z = 6.10;
camera.lookAt(scene.position)

let camera2 = new PerspectiveCamera(fieldOfView, window.innerWidth / window.innerHeight, 0.1, 1000)
var activeCamera: PerspectiveCamera = camera; 

window.addEventListener( 'resize', onWindowResize, false );

function onWindowResize(){
	let ratio = window.innerWidth / window.innerHeight;
	
    camera.aspect = ratio;
    camera.updateProjectionMatrix();

	renderer.setSize( window.innerWidth, window.innerHeight );
}


let renderer = new WebGLRenderer({ antialias: true })
renderer.setClearColor( 0x000000, 0 );
renderer.autoClear = false; 
renderer.setSize(window.innerWidth, window.innerHeight)

document.body.appendChild(renderer.domElement)

var control = new THREE.TransformControls(camera,renderer.domElement);
control.setSpace( "local" );
scene.add(control);

window.addEventListener( 'keydown', function ( event ) {
	switch ( event.keyCode ) {
		case 87: // W
			control.setMode( "translate" );
			break;
		case 69: // E
			control.setMode( "rotate" );
			break;
	}
});

scene.add( new GridHelper( 10, 10 ));

var gui = new GUI();
var addModelGUI = gui.addFolder("Add Model");

var sceneConfig = {

	AddStylisedFemaleModel (){

		if (numberOfAdds >= 1) return;
		
		numberOfFmAdds += 1;
		var hash = tempHash + numberOfFmAdds;
		var bodyGUI = gui.addFolder("Stylised Female" + numberOfFmAdds);
		var source = new LocalBodySource();
		
		var sourceBody: PivotedBodyPart = source.getBody(BodyType.FEMALE_STYLISED,hash);
		scene.add(sourceBody);
		sourceBody.bindGUI(bodyGUI);

		TAG_EVENT("App::AddStylisedFemale");
		
	},

	AddRodModel (){

		numberOfAdds += 1;
		var hash = tempHash + numberOfAdds;
		var bodyGUI = gui.addFolder("Rod" + numberOfAdds);
		var source = new LocalBodySource();
		
		var sourceBody: PivotedBodyPart = source.getBody(BodyType.ROD,hash);
		scene.add(sourceBody);
		sourceBody.bindGUI(bodyGUI);

		TAG_EVENT("App::AddRodModel");
		
	}
}

addModelGUI.add(sceneConfig, 'AddStylisedFemaleModel').name("Stylised Female model");
addModelGUI.add(sceneConfig, 'AddRodModel').name("Rod model");



var lastSelectedObject = undefined;
var raycaster = new Raycaster();

function animate(): void {
	requestAnimationFrame(animate);

	raycaster.setFromCamera( mouse, camera );

	var objects = scene.children;

	var intersects = raycaster.intersectObjects( objects,true );

	var foundObject = false;
	var hitPlanes = false;
	var hitMesh = false;

	for ( var i = 0; i < intersects.length; i++ ) {

		foundObject = true;

		var object = intersects[i].object;

		if (object instanceof Mesh){
			var obj: MeshWithParent = intersects[ i ].object as MeshWithParent;

			var parent: PivotedBodyPart = obj.parentRef;

			if (parent != undefined){

				if (lastSelectedObject != undefined && lastSelectedObject != parent){
					lastSelectedObject.setSelected(false);
				}

				parent.setSelected(true);

				if (control.getMode() == "rotate"){
					control.visible = parent.canRotate;

					if (parent.canRotate){
						control.attach(parent);
					}
				}

				else if (control.getMode() === "translate"){
					control.visible = parent.canMove;

					if (parent.canMove){
						control.attach(parent);
					}
				}

				lastSelectedObject = parent;
				hitMesh = true;
			}

			i = 99999;
		}

		//planes
		if (object.name == "XYZE" || object.name == "YZ" || object.name == "XZ" || object.name == "XY") {
			hitPlanes = true;
		}
	}

	//hit something else
	if (hitMesh == false && hitPlanes == true){

		if (lastSelectedObject != undefined){

			if(dragFlag === 0){
				lastSelectedObject.setSelected(false);
				control.detach();

			}
		}
	}

	renderer.clear();
	renderer.render(scene, camera);
	renderer.clearDepth();
}



requestAnimationFrame(animate);
TAG_EVENT("App::Open");


